type RankListProps = {
  ranks: gacha.Rank[];
}

const RankList = ({ ranks }: RankListProps) => {
  return (
    <ol start={4}>
      {
        ranks.map((rank) => (
          <li><strong>{rank.discord_id}</strong>, niveau {rank.level.currentLevel} avec {rank.currentXP}xp</li>
        ))
      }
    </ol>
  )
}

export default RankList
