import { useEffect, useState, useRef } from "react";

type CardProps = {
  id: number;
  img: string;
  title: string;
  type: 'basic' | 'gold';
  description: string;
  level: number;
  className?: string;
  isFusion?: boolean;
}

const Card = ({ id, img, title, description, level, type = 'basic', isFusion = false, className = '' }: CardProps) => {
  const [style, updateStyle] = useState({ currentWidth: 0, fontSize: '40px' });
  const ref = useRef<HTMLDivElement>(null);
  useEffect(() => {
    const calculateFont = () => {
      if (ref.current && style.currentWidth !== ref.current.clientWidth) {
        updateStyle({
          currentWidth: ref.current.clientWidth,
          fontSize: `${40 * ref.current.clientWidth / 475}px`
        })
      }
    }

    calculateFont();
    window.addEventListener('resize', calculateFont);
    return () => {
      window.removeEventListener('resize', calculateFont);
    }
  }, [])

  return (
    <div ref={ref} className={`${className} ${type} card-container`} style={{ fontSize: style.fontSize }}>
      <div className="card">
        <div className="card-header">
          {isFusion ? <div className="card-fusion">Fusion</div> : null}
          <div className="card-id">#{id}</div>
        </div>
        <div className="card-img" style={{ backgroundImage: `url("/images${img}")` }}></div>
        <div className="card-title">
          {title}
        </div>
        <div className="card-separator">
          <div className="card-line"></div>
        </div>
        <div className="card-description">{description}</div>
        <div className="card-stars">
          {[...Array(level)].map((_, index) => (
            <div className="card-star" key={index}></div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Card;
