import { useEffect, useState } from "react";
import { fetchRanks } from "../../API/ranksAPI";
import Rank from "../../components/Rank";
import RankList from "../../components/RankList";
import "./GachaRanks.css";

const GachaRanks = () => {
  const [isLoading, setLoader] = useState(true)
  const [ranks, updateRanks] = useState([] as gacha.Rank[])

  useEffect(() => {
    fetchRanks().then((ranks: gacha.Rank[]) => {
      updateRanks(ranks)
      setLoader(false)
    })
  }, [])

  if (isLoading) {
    return 'Loading'
  }

  const [top1, top2, top3, ...others] = ranks;

  return (
    <div className="gacha-ranks">
      <div className="gacha-top-ranks">
        <Rank rank={top1} level={1} />
        <Rank rank={top2} level={2} />
        <Rank rank={top3} level={3} />
      </div>
      <div className="gacha-rank-list">
        <RankList ranks={others} />
      </div>
    </div>
  )
}

export default GachaRanks
