export const fetchRanks = async (): Promise<gacha.Rank[]> => {
  const myInit: RequestInit = {
    method: 'GET',
    headers: {
      Accept: "application/json",
    }
  };

  return new Promise((resolve, reject) => {
    fetch('/api/ranks', myInit)
      .then((response) => response.json())
      .then(resolve)
      .catch(reject);
  })
}
