export const fetchAllCards = async ({ filters = {} }: gacha.AllCardsParams): Promise<gacha.CardType[]> => {
  const myInit: RequestInit = {
    method: 'GET',
    headers: {
      Accept: "application/json",
    }
  };

  return new Promise((resolve, reject) => {
    fetch(`/api/cards?filters=${encodeURI(
      JSON.stringify(filters)
    )}`, myInit)
      .then((response) => response.json())
      .then(resolve)
      .catch(reject);
  })
}
