import React, { useContext, useEffect, useState } from 'react';
import { fetchAllCards } from '../../API/cardsAPI';

//-- Types
type Filters = {
  gold: boolean;
  fusion: boolean;
  filterStars: string;
  search: string;
}

type GachaHomeContextProvider = {
  filters: Filters;
  updateFilters: React.Dispatch<React.SetStateAction<Filters>>
  cards: gacha.CardType[];
  cardSelected: gacha.CardType | null;
  selectCard: (card: gacha.CardType|null) => void;
  filterPanelState: boolean;
  toggleFilterPanelState: () => void;
};
type GachaHomeContextProps = {
  children: JSX.Element|JSX.Element[]|null;
};

//-- Context declaration

export const GachaHomeContext = React.createContext<GachaHomeContextProvider>({
  filters: {
    gold: false,
    fusion: false,
    filterStars: 'all',
    search: ''
  },
  updateFilters: () => null,
  cards: [],
  cardSelected: null,
  selectCard: () => {},
  filterPanelState: false,
  toggleFilterPanelState: () => {}
});

export const useGachaHome = (): GachaHomeContextProvider => useContext(GachaHomeContext);

//-- Exposed Provider
export const GachaHomeProvider = ({
  children,
}: GachaHomeContextProps): JSX.Element => {
  const [state, updateState] = useState({
    gold: false,
    fusion: false,
    filterStars: 'all',
    search: ''
  })
  const [cards, updateCards] = useState([] as gacha.CardType[])
  const [cardSelected, updateCardSelected] = useState<gacha.CardType | null>(null)
  const [filterPanelState, updateFilterPanelState] = useState(false)

  useEffect(() => {
    fetchAllCards({
      filters: {
        ...(state.filterStars === 'all' ? {} : { level: state.filterStars.length }),
        search: state.search,
        fusion: state.fusion
      }
    }).then((cards: gacha.CardType[]) => {
      updateCards(cards);
    })
  }, [state.filterStars, state.search, state.fusion])

  const providerParameters = {
    filters: state,
    updateFilters: updateState,
    cards,
    cardSelected,
    selectCard: (card: gacha.CardType|null) => {
      updateCardSelected(card)
    },
    filterPanelState,
    toggleFilterPanelState: () => {
      updateFilterPanelState(!filterPanelState)
    }
  }

  return (
    <GachaHomeContext.Provider value={providerParameters}>
      {children}
    </GachaHomeContext.Provider>
  );
};
