import Card from "../../../components/Card";
import FusionDetails from "../../../components/FusionDetails";
import { useGachaHome } from "../GachaHomeContext";

export const GachaHomeMobileView = () => {
  const { cardSelected, filters } = useGachaHome();

  if (!cardSelected) {
    return null;
  }

  return (
    <div className="gacha-home-mobile-view">
      <div className="gacha-home-mobile-view-card-block">
        <Card
          className={`scale-horizontally quarter ${filters.gold ? "gold" : ""}`}
          id={cardSelected.id}
          img={cardSelected.imageName}
          title={cardSelected.name}
          description={cardSelected.description}
          level={cardSelected.level}
          isFusion={cardSelected.isFusion}
          type={filters.gold ? "gold" : "basic"}
        />
      </div>
      {
        cardSelected.isFusion ? (
          <FusionDetails card={cardSelected} />
        ) : null
      }
    </div>
  );
}
